import { Component } from '@angular/core';
import { BaseUIComponent, DataModelProperties, ComponentExample, UIModel,
  ComponentDescriptor, Categories, propDescription, PropertyCategories } from '@ngx-dynamic-components/core';

@Component({
  selector: 'dc-chart', // tslint:disable-line
  templateUrl: './chart.component.html',
  styleUrls: ['./chart.component.scss']
})
export class ChartComponent extends BaseUIComponent<ChartProperties> {}

export class ChartProperties extends DataModelProperties {
  @propDescription({
    description: 'Chart header',
    example: 'New chart'
  })
  header: string;

  @propDescription({
    description: 'Chart X axis',
    example: 'propName',
    link: 'https://www.grapecity.com/wijmo/docs/Topics/Chart/Basic/Data-Binding#data-binding-in-flexchart'
  })
  bindingX: string;

  @propDescription({
    description: 'Selection in Chart',
    example: 'Point|Series|None',
    link: 'https://www.grapecity.com/wijmo/docs/Topics/Chart/Advanced/Selection'
  })
  selectionMode?: string;

  @propDescription({
    description: 'Legend position',
    example: 'Bottom',
    link: 'https://www.grapecity.com/wijmo/docs/Topics/Chart/Advanced/Legend-Titles#setting-the-legend-title-position'
  })
  legendPosition?: string;

  @propDescription({
    description: 'Chart series',
    example: '[{name: "Name", binding: "propName"}]',
    link: 'https://www.grapecity.com/wijmo/docs/Topics/Chart/Advanced/Legend-Titles#setting-the-legend-title-position'
  })
  series?: {name: string, binding: string}[];

  @propDescription({
    description: 'ChartType Enumeration',
    example: '5',
    link: 'https://www.grapecity.com/wijmo/api/enums/wijmo_chart.charttype.html'
  })
  chartType?: number;
}

