import { Component, OnInit, ViewChild, HostBinding } from '@angular/core';
import { switchMap } from 'rxjs/operators';
import { SelectionMode, FlexGrid, CellRangeEventArgs } from 'wijmo/wijmo.grid';
import { CollectionView, DataType, format } from 'wijmo/wijmo';
import { FlexGridXlsxConverter } from 'wijmo/wijmo.grid.xlsx';
import { transposeData, formatCamelStr } from 'src/app/shared';
import { ColumnInfo, GridUIState } from '../../models';
import { DialogService, FileManagerService } from 'src/app/shared/services';
import { BaseUIComponent, ComponentExample, DataModelProperties,
  ComponentDescriptor, UIModel, propDescription, JSONUtils, Categories } from '@ngx-dynamic-components/core';
import { FileType } from 'src/app/shared/models';
import { GrigConfig } from './grid-config';
import { ColumnEditorComponent } from '../column-editor/column-editor.component';

@Component({
  selector: 'dc-object-explorer',  // tslint:disable-line
  templateUrl: './grid-object-explorer.component.html',
  styleUrls: ['./grid-object-explorer.component.scss'],
  // changeDetection: ChangeDetectionStrategy.OnPush // Use for better performance
})
export class GridObjectExplorerComponent extends BaseUIComponent<GridObjectExplorerProperties> implements OnInit {

  @ViewChild('grid', {static: true}) grid: FlexGrid;
  @HostBinding('class') flex = GridUIState.hide;

  viewData: any = {};
  isVertical = true;
  objectPath: ObjectPath[] = [];
  columnsList: ColumnInfo[];
  collectionView = new CollectionView();
  localGridConfig: GrigConfig;
  private columnsToEdit: ColumnInfo[];
  private gridIdentifier: string;
  protected data: any;

  get itemsSource() {
    if (this.componentDataModel !== this.data) {
      this.data = this.componentDataModel;
      this.renderData(this.data);
      window.requestAnimationFrame(() => {
        this.grid.selectionMode = SelectionMode.CellRange;
        this.objectPath = [];
        const state = this.data ? GridUIState.show : GridUIState.hide;
        if (this.flex !== state) {
          this.flex = state;
        }
      });
    }

    return this.collectionView;
  }

  get fieldsCount() {
    return this.collectionView.items.length;
  }

  constructor(protected fileService: FileManagerService, protected dialogService: DialogService) {
    super();
  }

  async ngOnInit() {
    if (this.properties.isReadOnly === undefined) {
      this.uiModel.itemProperties.isReadOnly = true;
    }
    this.gridIdentifier = this.properties.host;
    if (this.gridIdentifier) {
      this.localGridConfig = new GrigConfig(this.gridIdentifier, this.uiModel.id || 'object-explorer');
      this.grid.addEventListener(this.grid.hostElement, 'contextmenu', (e: CellRangeEventArgs) => {
        const ht = this.grid.hitTest(e);
        this.columnsToEdit = this.columnsList.filter((v, i) => {
          const {col2, col} = this.grid.selection;
          if (col2 - col > 0) {
            return this.grid.selection.col2 >= i && this.grid.selection.col <= i || ht.col === i;
          }
          return this.grid.selection.col >= i && this.grid.selection.col2 <= i || ht.col === i;
        });
      });
    }

    this.grid.onCellEditEnded = (e: CellRangeEventArgs) => {
      let data = this.data;
      if (this.isVertical) {
        this.objectPath.forEach((o: ObjectPath) => {
          data = data[o.path];
        });

        const item = this.collectionView.sourceCollection[e.row];

        // Delete if property name was changed
        if (e.col === 0 && e.data !== item.fieldName) {
          delete data[e.data];
        }

        data[item.fieldName] = item.row0 === undefined ? '' : item.row0;
      } else {
        let dataToSet = this.collectionView.sourceCollection;
        if (dataToSet.length && dataToSet[0].valueTypeKey) {
          dataToSet = dataToSet.map(v => v.valueTypeKey);
        }
        JSONUtils.setValue(this.data, this.getJSONUtilsPath(), dataToSet);
      }
      this.changedDataModel.emit(this.data);
    };
    this.grid.onDeletingRow = (e) => {
      const data = this.objectPath.reduce((prev, prop) => {
        return prev[prop.path];
      }, this.data);
      if (this.isVertical) {
        this.grid.selectedItems.forEach(i => {
          delete data[i.fieldName];
        });
      } else {
        data.splice(e.row, 1);
      }
      this.changedDataModel.emit(this.data);
      return true;
    };
    await super.ngOnInit();
  }

  renderData(data: any) {
    let value = data;
    if (!data) {
      value = [];
    }
    if (!Array.isArray(value)) {
      this.isVertical = true;
      this.columnsList = this.getVerticalColumns();
      value = this.formatToArray(value);
    } else {
      this.isVertical = false;
      this.columnsList = this.getColumns(value);
      value = this.formatData(value);
    }

    this.grid.selectionMode = SelectionMode.None;
    this.viewData = data;
    this.collectionView.sourceCollection = value;
    window.requestAnimationFrame(() => {
      this.grid.selectionMode = SelectionMode.CellRange;
    });
  }

  onDataSelect(item: any, column: ColumnInfo) {
    const data = item[column.bindingName];
    if (data && typeof data === 'object') {
      if (!this.isVertical) {
        this.objectPath.push({
          path: this.collectionView._idx,
          dataItem: this.grid.selectedRows[0]
        });
      }
      this.objectPath.push({
        path: this.isVertical ? item.fieldName : column.bindingName,
        dataItem: this.grid.selectedRows[0].dataItem
      });
      this.renderData(data);
    }
  }

  onResizedColumn(evt: CellRangeEventArgs) {
    if (this.localGridConfig) {
      this.localGridConfig.saveColumn(this.grid.columns[evt.col].binding, {
        width: this.grid.columns[evt.col].renderWidth
      }, this.getPathStr());
    }
  }

  onCtxMenuClicked(menu) {
    if (!this.localGridConfig) {
      alert('Add host item property to UIModel');
    } else {
      const columnEditDialog = this.dialogService.open(ColumnEditorComponent, {class: 'modal-lg'});
      columnEditDialog.config = this.columnsToEdit;
      columnEditDialog.init.pipe(switchMap(() => columnEditDialog.component.save)).subscribe((columns) => {
        (columns as ColumnInfo[]).forEach(column => {
          this.localGridConfig.saveColumn(column.bindingName, column, this.getPathStr());
        });
        this.renderData(this.viewData);
      });
    }
  }

  getDefaultHeader(column: ColumnInfo): string {
    return formatCamelStr(column.bindingName);
  }

  getColumns(data: any[] = []): ColumnInfo[] {
    if (data.length) {
      const item = data.reduce((prev, el) => {
        if (typeof el === 'object') {
          Object.assign(prev, el);
        } else {
          Object.assign(prev, {valueTypeKey: el});
        }
        return prev;
      }, {});
      return Object.keys(item).map(prop => {
        if (prop === 'valueTypeKey') {
          return {
            headerText: 'Value',
            bindingName: 'valueTypeKey'
          };
        }
        return {
          headerText: this.getColumnHeader(prop) || prop,
          bindingName: prop,
          width: this.getColumnWidth(prop),
          dataType: this.getColumnType(prop),
          displayFormat: this.getColumnDisplayFormat(prop)
        };
      });
    }
  }

  getVerticalColumns() {
    return [{
      headerText: this.getColumnHeader('fieldName'),
      bindingName: 'fieldName',
      width: this.getColumnWidth('fieldName', 220),
      dataType: this.getColumnType('fieldName'),
      displayFormat: this.getColumnDisplayFormat('fieldName')
    }, {
      headerText: 'Field Value',
      bindingName: 'row0',
      width: this.getColumnWidth('row0', '*'),
      dataType: this.getColumnType('row0') || DataType.String,
      displayFormat: this.getColumnDisplayFormat('row0')
    }];
  }

  selectPath(index: number) {
    this.objectPath = this.objectPath.slice(0, index + 1);
    let data = this.objectPath.reduce((prev, prop) => {
      return prev[prop.path];
    }, this.data);

    if (!data) {
      const last = this.objectPath.slice(-1);
      data = last.length ? last[0].dataItem[last[0].path] : null;
    }

    this.renderData(data);
  }

  isObject(item: any) {
    return item && typeof item === 'object' && Object.keys(item).length;
  }

  formatToArray(data) {
    return transposeData([Object.entries(data).reduce((res, [key, val]) => {
      res[key] = val;
      return res;
    }, {})]);
  }

  formatData(data: any[] = []) {
    if (data.length) {
      return data.map(value => {
        if (typeof value !== 'object') {
          value = {valueTypeKey: value};
        }
        return value;
      });
    }
    return data;
  }

  formatToStr(item: object) {
    const str = JSON.stringify(item);
    return str.length > 250 ? `${str.slice(0, 250)}...` : str;
  }

  formatValue(item: object, column: ColumnInfo) {
    const {dataType, displayFormat, bindingName} = column;
    const val = item[bindingName];
    switch (dataType) {
      case DataType.Date: {
        if (val === null) {
          return '';
        }
        return displayFormat ? format(`{val:${displayFormat}}`, {val: new Date(val)}) : new Date(val).toDateString();
      }
      case DataType.Number:
        if (isNaN(val) || val === '') {
          return val;
        }
        return displayFormat ? format(`{val:${displayFormat}}`, {val: Number(val)}) : Number(val);
      case DataType.Boolean: return Boolean(val);
    }
    if (typeof val === 'object' && !(val instanceof Date)) {
      if (Array.isArray(val) && !val.length) {
        return '[]'; // '{Empty array}';
      } else if (val && Object.keys(val).length) {
        return this.formatToStr(val);
      } else if (val) {
        return '{}'; // '{Empty object}';
      } else if (val === null) {
        return ''; // '{null}';
      }
    }
    return val;
  }

  exportToCSV() {
    this.fileService.exportToFile(this.collectionView.sourceCollection, {
      fileType: FileType.CSV,
      fileName: this.objectPath.map(o => o.path).join('-') || 'object-explorer'
    });
  }

  exportToExcel() {
    FlexGridXlsxConverter.saveAsync(this.grid, { includeColumnHeaders: true }, 'object-explorer.xlsx');
  }

  getColumnWidth(binding: string, defaultWidth: number | '*' = 150) {
    return (this.localGridConfig && this.localGridConfig.getColumn(binding, this.getPathStr()).width) || defaultWidth;
  }

  getColumnHeader(binding: string) {
    return (this.localGridConfig && this.localGridConfig.getColumn(binding, this.getPathStr()).header);
  }

  getColumnType(binding: string) {
    return (this.localGridConfig && this.localGridConfig.getColumn(binding, this.getPathStr()).dataType);
  }

  getColumnDisplayFormat(binding: string) {
    return (this.localGridConfig && this.localGridConfig.getColumn(binding, this.getPathStr()).displayFormat);
  }

  private getPathStr() {
    return this.objectPath.map(o => o.path).join('-') || 'root';
  }

  private getJSONUtilsPath() {
    if (this.objectPath.length === 0) {
      return '$';
    }

    return '$.' + this.objectPath.map(o => {
      if (typeof o.path === 'string') {
        return o.path.replace(/\//g, ':');
      }
      return o.path;
    }).join('/');
  }
}

export class GridObjectExplorerProperties extends DataModelProperties {
  /** @details https://www.grapecity.com/wijmo/api/classes/wijmo_grid.flexgrid.html#isreadonly */
  @propDescription({
    description: 'Determines whether the user can modify cell. Default true',
    example: 'true',
    link: 'https://www.grapecity.com/wijmo/api/classes/wijmo_grid.flexgrid.html#isreadonly'
  })
  isReadOnly?: boolean;
  @propDescription({
    description: 'Defines identifier for grid',
    example: 'grid-id-1'
  })
  host?: string;
  /** @details https://www.grapecity.com/wijmo/api/enums/wijmo_grid.selectionmode.html */
  @propDescription({
    description: 'Specifies constants that define the selection behavior.',
    example: '5',
    link: 'https://www.grapecity.com/wijmo/api/enums/wijmo_grid.selectionmode.html'
  })
  selectionMode?: SelectionMode;
  /** @details https://www.grapecity.com/wijmo/api/classes/wijmo_grid.flexgrid.html#allowdelete */
  @propDescription({
    description: 'Specifies if row can be deleted. Default false',
    example: 'true',
    link: 'https://www.grapecity.com/wijmo/api/classes/wijmo_grid.flexgrid.html#allowdelete'
  })
  allowDelete?: boolean;

  /** @details https://www.grapecity.com/wijmo/api/classes/wijmo_grid.flexgrid.html#allowaddnew */
  @propDescription({
    description: 'Specifies if new row can be added. Default false',
    example: 'true',
    link: 'https://www.grapecity.com/wijmo/api/classes/wijmo_grid.flexgrid.html#allowaddnew'
  })
  allowAddNew?: boolean;
}

