import { formatCamelStr } from 'src/app/shared';

export class GrigConfig {
  private dataKey: string;
  private gridId: string;
  private gridConfig: object;
  constructor(dataKey: string, gridId: string) {
    this.dataKey = dataKey;
    this.gridId = gridId;

    this.initConfig();
  }

  getColumn(binding: string, path: string = 'root') {
    if (this.gridConfig[path] && this.gridConfig[path][binding]) {
      const column = this.gridConfig[path][binding];
      return {
        header: column.headerText || formatCamelStr(binding),
        width: column.width,
        dataType: column.dataType ? Number(column.dataType) : null,
        displayFormat: column.displayFormat || null
      };
    }
    return {
      header: formatCamelStr(binding),
      dataType: null
    };
  }

  saveColumn(binding: string, config: object, path: string = 'root') {
    if (this.gridConfig[path]) {
      this.gridConfig[path][binding] = {...this.gridConfig[path][binding], ...config};
    } else {
      this.gridConfig[path] = {[binding]: {...config}};
    }

    const gridConfigs = this.getConfigs();

    if (!gridConfigs[this.dataKey]) {
      gridConfigs[this.dataKey] = {
        [this.gridId]: this.gridConfig
      };
    } else {
      gridConfigs[this.dataKey][this.gridId] = this.gridConfig;
    }
    localStorage.setItem('grid-configs', JSON.stringify(gridConfigs));
  }

  private initConfig() {
    const gridConfigs = this.getConfigs();
    if (!gridConfigs[this.dataKey]) {
      gridConfigs[this.dataKey] = {
        [this.gridId]: { }
      };
    } else {
      const config = gridConfigs[this.dataKey][this.gridId];
      if (!config) {
        gridConfigs[this.dataKey][this.gridId] = {};
      }
    }

    this.gridConfig = gridConfigs[this.dataKey][this.gridId];
  }

  private getConfigs() {
    const savedData = localStorage.getItem('grid-configs');
    let gridConfigs = {};
    if (savedData) {
      try {
        gridConfigs = JSON.parse(savedData) || {};
      } catch (e) {
        gridConfigs = {};
      }
    }

    return gridConfigs;
  }
}
