import { Component, ViewChild } from '@angular/core';
// import Wijmo components
import { CollectionView } from 'wijmo/wijmo';
import { BaseUIComponent, ComponentExample, DataModelProperties,
  propDescription, ComponentDescriptor, UIModel, Categories } from '@ngx-dynamic-components/core';

import { FlexGridFilter } from 'wijmo/wijmo.grid.filter';
import { SelectionMode, HeadersVisibility, AllowResizing, AllowMerging, FlexGrid } from 'wijmo/wijmo.grid';
import { ColumnInfo } from '../../models';
import { formatCamelStr, transposeData } from 'src/app/shared/utils';

@Component({
  selector: 'dc-grid', // tslint:disable-line
  template: `
    <h3 class="h3" *ngIf="properties.title">{{properties.title}}</h3>
    <wj-flex-grid #grid [itemsSource]="itemsSource" [ngStyle]="itemStyles"
      [allowResizing]="properties.allowResizing || defaultProperties.allowResizing"
      [allowMerging]="properties.allowMerging || defaultProperties.allowMerging"
      [stickyHeaders]="properties.stickyHeaders || defaultProperties.stickyHeaders"
      [selectionMode]="properties.selectionMode || defaultProperties.selectionMode"
      [frozenRows]="properties.frozenRows || 0"
      [frozenColumns]="properties.frozenColumns || 0"
      [headersVisibility]="headersVisibility"
      [allowDelete]="properties.allowDelete || false"
      [isReadOnly]="properties.isReadOnly || false">
      <ng-container *ngFor="let column of columnsList">
        <wj-flex-grid-column *ngIf="!column.details"
          [header]="column.headerText || getDefaultHeader(column)"
          [binding]="column.bindingName"
          [width]="column.width || null"
          [dataMap]="column.dataMap"
          [dataType]="column.dataType"
          [format]="column.displayFormat">
          </wj-flex-grid-column>

          <wj-flex-grid-column *ngIf="column.details" header="Actions">
            <ng-template wjFlexGridCellTemplate cellType="Cell" let-item="item">
              <a [routerLink]="['../', properties.details.pageName]"
              [queryParams]="getQueryParams(item)">Details</a>
            </ng-template>
        </wj-flex-grid-column>
      </ng-container>

      <wj-flex-grid-filter #filter></wj-flex-grid-filter>
    </wj-flex-grid>
  `
})
export class GridComponent extends BaseUIComponent<GridProperties> {

  @ViewChild('filter', {static: false}) filter: FlexGridFilter;
  @ViewChild('grid', {static: false}) grid: FlexGrid;

  private _componentDataModel: any;

  private _collectionView: CollectionView;

  private _dataProperties: string[] = [];

  private _columnsKey: string;

  private _columns: ColumnInfo[] = [];

  defaultProperties: GridProperties = {
    allowResizing: AllowResizing.Both,
    allowMerging: AllowMerging.All,
    stickyHeaders: false,
    selectionMode: SelectionMode.Cell
  };

  get columnsList(): ColumnInfo[] {
    if (this.properties.columns && this.properties.columns.length) {
      return this.properties.columns;
    }

    this.getColumns();
    return this._columns;
  }

  private getColumns() {
    if (this.getColumnsKey() !== this._columnsKey) {
      const columns = [];

      if (this.properties.removeColumns && this.properties.removeColumns.length) {
        this._dataProperties = this._dataProperties.filter(prop => {
          return !this.properties.removeColumns.includes(prop);
        });
      }

      this._dataProperties.forEach(prop => {
        const column = (this.properties.overrideColumns || []).find(o => o.bindingName === prop);
        if (column) {
          columns.push(column);
        } else {
          columns.push({ headerText: formatCamelStr(prop), bindingName: prop });
        }
      });

      this._columnsKey = this.getColumnsKey();
      window.requestAnimationFrame(() => {
        this._columns = columns;
      });
    }
  }

  private getColumnsKey() {
    if (this.properties.overrideColumns || this.properties.removeColumns) {
      return JSON.stringify(this.properties.overrideColumns) +
      JSON.stringify(this.properties.removeColumns) +
      JSON.stringify(this._dataProperties);
    }
  }

  get headersVisibility() {
    return this.properties.hasOwnProperty('headersVisibility') ? this.properties.headersVisibility : HeadersVisibility.All;
  }

  get itemsSource() {
    if (this._componentDataModel && this.componentDataModel === this._componentDataModel) {
      return this._collectionView;
    }

    this._componentDataModel = this.componentDataModel;
    const data = this.properties.isVertical ? transposeData(this.componentDataModel) : this.componentDataModel;
    if (data && data.length) {
      this._dataProperties = Object.keys(data[0]);
    }
    this._collectionView = new CollectionView(data);
    return this._collectionView;
  }

  getQueryParams(item) {
    return {[this.properties.details.idField]: item[this.properties.details.idField]};
  }

  getDefaultHeader(column: ColumnInfo) {
    return formatCamelStr(column.bindingName);
  }
}

export class GridProperties extends DataModelProperties {
  @propDescription({
    description: 'Allow resizing',
    example: '3',
  })
  allowResizing?: AllowResizing;

  /** @details https://www.grapecity.com/wijmo/api/enums/wijmo_grid.selectionmode.html */
  @propDescription({
    description: 'Specifies constants that define the selection behavior.',
    example: '5'
  })
  selectionMode?: SelectionMode;

  /** @details https://www.grapecity.com/wijmo/api/enums/wijmo_grid.allowmerging.html */
  @propDescription({
    description: 'Specifies constants that define which areas of the grid support cell merging.',
    example: '7'
  })
  allowMerging?: AllowMerging;

  @propDescription({
    description: 'Property to ensure column headers remain visible when you scroll the document.',
    example: 'true'
  })
  stickyHeaders?: boolean;

  @propDescription({
    description: 'Specifies list of columns to display.',
    example: `[{
      headerText: 'Header title',
      bindingName: 'propName',
      dataType: 'Number'
    }]`
  })
  columns?: ColumnInfo[];

  @propDescription({
    description: 'Amount of first frozen rows.',
    example: '3'
  })
  frozenRows?: string;

  @propDescription({
    description: 'Amount of first frozen columns.',
    example: '2'
  })
  frozenColumns?: string;

  /** @details https://www.grapecity.com/wijmo/api/enums/wijmo_grid.headersvisibility.html */
  @propDescription({
    description: 'Specifies constants that define the visibility of row and column headers.',
    example: '3'
  })
  headersVisibility?: HeadersVisibility;

  /** @details https://www.grapecity.com/wijmo/api/classes/wijmo_grid.flexgrid.html#allowdelete */
  @propDescription({
    description: 'Specifies if row can be deleted. Default false',
    example: 'true'
  })
  allowDelete?: boolean;

  /** @details https://www.grapecity.com/wijmo/api/classes/wijmo_grid.flexgrid.html#isreadonly */
  @propDescription({
    description: 'Determines whether the user can modify cell. Default false',
    example: 'true'
  })
  isReadOnly?: boolean;

  @propDescription({
    description: 'Specify if grid should render data vertically',
    example: 'true'
  })
  isVertical?: boolean;

  @propDescription({
    description: 'Specifies list of columns that should be overriden.',
    example: `[{
      headerText: 'Header title',
      bindingName: 'propName',
      dataType: 'Number'
    }]`
  })
  overrideColumns?: ColumnInfo[];

  @propDescription({
    description: 'Specifies list of columns that should be removed.',
    example: `['prop1', 'prop2']`
  })
  removeColumns?: string[];

  details?: any;

  title?: string;
}

