interface GridComponentConstrutor {
  new (): GridComponent;
}

interface GridPropertiesConstrutor {
  new (): GridProperties;
}

export const gridDescriptor: ComponentDescriptor<GridComponentConstrutor, GridPropertiesConstrutor> = {
  name: 'grid',
  packageName: 'wijmo',
  label: 'DataGrid',
  category: Categories.Data,
  description: 'Data table component',
  itemProperties: GridProperties,
  component: GridComponent,
  example
};
