import { Component, Input, OnInit } from '@angular/core';
import { CollectionView, DataType } from 'wijmo/wijmo';
import { GridObjectExplorerComponent, GridObjectExplorerProperties } from '../grid-object-explorer/grid-object-explorer.component';
import { FileManagerService, DialogService, SqlDbService } from 'src/app/shared/services';
import { ComponentDescriptor, propDescription, UIModel, ComponentExample } from '@ngx-dynamic-components/core';
import { Categories } from '@ngx-dynamic-components/material/lib/constants';
import { GridContext, initChangesHighlight } from '../../utils';
import { ColumnInfo, GridUIState } from '../../models';
import { FlexGrid, CellType, CellRangeEventArgs } from 'wijmo/wijmo.grid';
import { TableDto, SelectBody, FilterSqlData, WijmoMapToSqlTypes } from 'src/app/shared/models';
import { mapTableToValues, getFilters, formatWijmoDate, dateToDtoString, mapValuesToTable } from 'src/app/shared';
import { switchMap } from 'rxjs/operators';
import { ColumnEditorComponent } from '../column-editor/column-editor.component';
import { GrigConfig } from '../grid-object-explorer/grid-config';
import { Observable } from 'rxjs';

@Component({
  selector: 'dc-data-grid', // tslint:disable-line
  styleUrls: ['./data-grid.component.scss'],
  templateUrl: './data-grid.component.html'
})
export class DataGridComponent extends GridObjectExplorerComponent implements OnInit {
  data: Object[] = [];
  loading = true;
  dataObject;
  filters: { [key: string]: any[] } = {};
  limitRows: number;
  isChanged$: Observable<boolean>;
  @Input() uiModel: UIModel<DataGridProperties>;
  changedData: any;
  private gridContext: GridContext;
  private columnToEdit: ColumnInfo;
  private contextMenuValue: any;

  constructor(protected fileService: FileManagerService, protected dialogService: DialogService, private sqlService: SqlDbService) {
    super(fileService, dialogService);
  }

  get properties() {
    return this.uiModel.itemProperties;
  }

  async saveData() {
    const {connection} = this.properties.source;
    await this.sqlService.saveSqlObjectData(connection, this.dataObject,
        {tableData: mapValuesToTable(this.changedData)}).toPromise();
    this.loadData();
  }

  async ngOnInit() {
    if (!this.properties.source) {
      return;
    }
    const {connection, owner, name} = this.properties.source;
    this.dataObject = {
      objectOwner: owner,
      objectName: name
    };
    const key = `${connection}-${owner}-${name}`;
    this.localGridConfig = new GrigConfig(key, this.uiModel.id || 'object-explorer');
    this.flex = GridUIState.show;
    await this.loadData();
  }

  getColumns(data: []): ColumnInfo[] {
    if (this.properties.columns && Array.isArray(this.properties.columns)) {
      return this.properties.columns.map(c => {
        if (!c.headerText) {
          c.headerText = this.getColumnHeader(c.bindingName);
        }
        c.dataType = this.getColumnType(c.bindingName) || DataType[c.dataType];
        c.width = this.getColumnWidth(c.bindingName) || c.width;
        if ([DataType.Date, DataType.Number].includes(c.dataType)) {
          c.displayFormat = this.getColumnDisplayFormat(c.bindingName) || c.displayFormat;
        } else {
          c.displayFormat = null;
        }
        return c;
      });
    }

    return super.getColumns(data);
  }

  getQueryParams(item) {
    return {[this.properties.details.idField]: item[this.properties.details.idField]};
  }

  private loadData() {
    this.loading = true;
    const {connection, owner, name} = this.properties.source;

    const query = this.properties.source.query;
    let selectBody: SelectBody = {};
    let filters = getFilters(this.filters) || [];
    if (query) {
      if (query.filters) {
        filters = [...filters, ...query.filters];
      }
      selectBody = {...query, filters};
    }

    this.sqlService.selectSqlObjectData(connection, owner, name, selectBody).subscribe(res => {
      this.changedData = null;
      if (Array.isArray(this.properties.columns)) {
        res.dataTypes = this.properties.columns.map(c => WijmoMapToSqlTypes[c.dataType] || null);
      }
      this.renderData(mapTableToValues(res));
      this.loading = false;
    });
  }

  get defaultFilters(): FilterSqlData[] {
    const query = this.properties.source.query;
    return query && query.filters;
  }

  onCtxMenuClicked(menu) {
    switch (menu.value) {
      case 'edit-column': this.editColumn(); break;
      case 'fill-all': this.fillRows('all'); break;
      case 'fill-empty': this.fillRows('empty'); break;
    }
  }

  onFiltersChange(filters: { [key: string]: any[] }) {
    this.filters = { ...filters };
    this.loadData();
  }

  onApply(evt: any[], field: ColumnInfo) {
    this.filters = { ...this.filters, [field.bindingName]: evt };
    this.loadData();
  }

  get filtersList() {
    return Object.entries(this.filters).filter(([fieldName, value]) => Boolean(value)).map(([fieldName, value]) => {
      return {
        fieldName, value: value.map(v => {
          const type = this.localGridConfig.getColumn(fieldName).dataType;
          if (type === DataType.Date) {
            const displayFormat = this.localGridConfig.getColumn(fieldName).displayFormat;
            return formatWijmoDate(v, displayFormat);
          }
          if (v instanceof Date) {
            return dateToDtoString(v);
          }
          if (v === null) {
            return '[NULL]';
          }
          return v;
        }).join(', ')
      };
    });
  }

  private editColumn() {
    const columnEditDialog = this.dialogService.open(ColumnEditorComponent, { class: 'modal-lg' });
    const columnInfo = this.columnToEdit;

    columnEditDialog.config = [columnInfo];
    columnEditDialog.init.pipe(switchMap(() => columnEditDialog.component.save)).subscribe((columns) => {
      (columns as ColumnInfo[]).forEach(c => {
        this.localGridConfig.saveColumn(c.bindingName, c);
      });
      const column = columns[0];
      const index = this.columnsList.findIndex(f => f.bindingName === column.bindingName);
      this.columnsList.splice(index, 1, column);
    });
  }

  private fillRows(key: string) {
    let isUpdated = false;
    this.grid.collectionView.sourceCollection.forEach((item) => {
      const binding = this.columnToEdit.bindingName;
      const oldVal = item[binding];
      if (this.updateCondition(oldVal, key)) {
        item[binding] = this.contextMenuValue;
        this.gridContext.setEditValue(item, binding, oldVal, this.contextMenuValue);
        isUpdated = true;
      }
    });
    if (isUpdated) {
      this.grid.refresh(true);
      this.changeHandler();
    }
  }

  private updateCondition(oldVal: any, key: string): boolean {
    switch (key) {
      case 'all': return oldVal !== this.contextMenuValue;
      case 'empty': return [null, undefined, ''].includes(oldVal);
      default: return true;
    }
  }

  initializedGrid(grid: FlexGrid) {
    this.gridContext = initChangesHighlight(grid);
    grid.cellEditEnded.addHandler(() => this.changeHandler());
    grid.deletedRow.addHandler(() => this.changeHandler());

    grid.addEventListener(grid.hostElement, 'contextmenu', (e: CellRangeEventArgs) => {
      const ht = grid.hitTest(e);
      this.columnToEdit = this.columnsList.find((v, i) => ht.col === i);
      const val = this.grid.collectionView.items[ht.row][this.columnToEdit.bindingName];
      this.contextMenuValue = val;
    });

    const sItemsFormatter = grid.itemFormatter;

    grid.itemFormatter = (panel, r, c, cell) => {
      sItemsFormatter(panel, r, c, cell);
      const binding = panel.columns[c].binding;
      if (panel.cellType === CellType.ColumnHeader && cell.firstChild instanceof HTMLElement) {
        const sortSpan = cell.firstChild.querySelector('#sort-icon');
        if (sortSpan) {
          cell.firstChild.removeChild(sortSpan);
        }
        const sortState = grid.collectionView.sortDescriptions[0];
        if (sortState && sortState.property === binding) {
          const span = document.createElement('SPAN');
          span.id = 'sort-icon';
          if (sortState.ascending) {
            span.className = 'wj-glyph-up';
          } else {
            span.className = 'wj-glyph-down';
          }
          cell.firstChild.appendChild(span);
        }
      }
    };

    grid.sortingColumn.addHandler((s, e: any) => {
      if (e.data.target.classList.contains('fa-filter')) {
        e.cancel = true;
      }
    });

    grid.resizingColumn.addHandler((s, evt: any) => {
      if (this.localGridConfig) {
        this.localGridConfig.saveColumn(grid.columns[evt.col].binding, {
          width: grid.columns[evt.col].renderWidth
        });
      }
    });
  }

  private changeHandler() {
    const { added, edited } = this.gridContext.state;
    let data = [];
    if (edited) {
      data = [...data, ...edited];
    }
    if (added) {
      data = [...data, ...added];
    }
    this.changedData = data;
  }
}

export class DataGridProperties extends GridObjectExplorerProperties {
  /** @details https://www.grapecity.com/wijmo/api/classes/wijmo_grid.flexgrid.html#isreadonly */
  @propDescription({
    description: 'Source',
    example: '{connection: "conectionName", owner: "OwnerName", name: "TableName"}',
  })
  source: {
    connection: string;
    owner: string;
    name: string;
    query?: SelectBody;
  };

  @propDescription({
    description: 'Specifies list of columns to display.',
    example: `[{
      headerText: 'Header title',
      bindingName: 'propName',
      dataType: 'Number'
    }]`
  })
  columns?: ColumnInfo[];

  details?: any;
}

interface DataGridComponentConstrutor {
  new (fileService?: FileManagerService, dialogService?: DialogService, sqlService?: SqlDbService): DataGridComponent;
}

interface DataGridPropertiesConstrutor {
  new (): DataGridProperties;
}

export const example: ComponentExample<UIModel<DataGridProperties>> = {
  title: 'Grid example',
  uiModel: {
    type: 'wijmo:grid',
    containerProperties: {
      width: '100%'
    },
    itemProperties: {
      source: {
        connection: 'sql-query',
        owner: 'test1',
        name: 'table1'
      }
    }
  },
  dataModel: null
};

export const dataGridDescriptor:
  ComponentDescriptor<DataGridComponentConstrutor, DataGridPropertiesConstrutor> = {
  name: 'data-grid',
  packageName: 'wijmo',
  label: 'Data grid',
  category: Categories.DataTable,
  description: 'Data Grid component',
  itemProperties: DataGridProperties,
  component: DataGridComponent,
  example
};
