import { CollectionView } from 'wijmo/wijmo';
import { FlexGrid, CellType, CellRangeEventArgs } from 'wijmo/wijmo.grid';

/**
 * Function provides data state changes for grid.
 */
export function initChangesHighlight(grid: FlexGrid): GridContext {
  const addCollectionSet = new Set();
  const editCollectionMap =  new Map<object, object>();
  const deleted = [];

  grid.rowAdded.addHandler((g, e) => {
    const collection = grid.collectionView as CollectionView;
    addCollectionSet.add(collection.currentAddItem);
    grid.refreshCells(false);
  });

  grid.onPasted = ((e: CellRangeEventArgs) => {
    const { range: {row, row2} } = e;
    const pastedItems = [];
    for (let index = row; index <= row2; index++) {
      pastedItems.push(grid.rows[index].dataItem);
      addCollectionSet.add(grid.rows[index].dataItem);
    }
    grid.refreshCells(false);
  });

  grid.deletingRow.addHandler((g, e) => {
    const collection = grid.collectionView as CollectionView;
    if (!addCollectionSet.has(collection.currentItem)) {
      deleted.push(collection.currentItem);
    } else {
      addCollectionSet.delete(collection.currentItem);
    }
    editCollectionMap.delete(collection.currentItem);
  });

  grid.itemFormatter = (panel, r, c, cell) => {
    const dataItem = panel.rows[r].dataItem;
    const binding = panel.columns[c].binding;
    const initData = editCollectionMap.get(dataItem);
    if (panel.cellType === CellType.Cell && dataItem) {
      if (addCollectionSet.has(dataItem)) {
        cell.classList.add('added-item');
      } else if (initData && initData.hasOwnProperty(binding)) {
        cell.classList.add('edited-item');
      } else {
        cell.classList.remove('added-item');
        cell.classList.remove('edited-item');
      }
    }
  };

  function setEditValue(item: any, binding: string, initVal: any, newVal: any): void {
    const initDataItem = editCollectionMap.get(item) || {};
    if (!initDataItem.hasOwnProperty(binding)) {
      initDataItem[binding] = initVal;
    }

    if (areValuesEqual(initDataItem[binding], newVal)) {
      delete initDataItem[binding];
    }
    editCollectionMap.set(item, initDataItem);
  }

  grid.cellEditEnded.addHandler((g, e: CellRangeEventArgs) => {
    const item = (grid.collectionView as CollectionView).currentEditItem;

    if (!addCollectionSet.has(item)) {
      const binding = grid.columns[e.col].binding;
      const newVal = grid.rows[e.row].dataItem[binding];
      setEditValue(item, binding, e.data, newVal);
    }
    grid.refreshCells(false);
  });

  const gridContext = {
    clear() {
      editCollectionMap.clear();
      addCollectionSet.clear();
    },
    get state() {
      return {
        edited: Array.from(editCollectionMap.keys()),
        added: Array.from(addCollectionSet.values()),
        deleted
      };
    },
    setEditValue
  };

  return gridContext;
}

function areValuesEqual(val1: any, val2: any) {
  if (val1 instanceof Date) {
    return val1.getTime() === val2.getTime();
  }
  return val1 === val2;
}

export interface GridContext {
  clear: () => void;
  state: any;
  setEditValue: (item: any, binding: string, initVal: any, newVal: any) => void;
}
